import React from 'react';
import { ethers as Ethers } from 'ethers';
import { hasFunction, isEqual } from '../utils/helper';
import { uInterval } from '../utils/tools';

export const MAX_UINT256 = Ethers.constants.MaxUint256;

export const ETH_TOKEN_ADDRESS = '0x0000000000000000000000000000000000000000';
export const ETH_TOKEN_DECIMALS = 18;

export function calcGasLimit(gasLimit) {
    // let result = Ethers.BigNumber.from(gasLimit);
    // console.log(gasLimit);
    // return result.mul(1.2);
    return gasLimit.mul(12).div(10);
}

export const REFRESH_INTERVAL = 10 * 1000;
export const useIntervalRefresh = (fn, delay = REFRESH_INTERVAL) => {
    const timer = React.useRef({ cancel: null, loop: null });

    const cancelTimer = React.useCallback(() => {
        const { cancel } = timer.current;
        if (hasFunction(cancel)) {
            cancel();
        }
        timer.current.cancel = null;
    }, []);

    if (!isEqual(timer.current.loop, fn)) {
        timer.current.loop = fn;
        if (hasFunction(fn)) {
            cancelTimer();
            timer.current.cancel = uInterval(async () => {
                try {
                    await timer.current.loop();
                    return true;
                } catch (error) {
                    return false;
                }
            }, delay);
        }
    }

    React.useEffect(() => {
        return cancelTimer;
    }, [cancelTimer]);
};
