import { hasFunction } from '../utils/helper';

export const waitForSth = (fnWhenSthHasValue, maxAttempts = 10, delay = 100) =>
    new Promise((resolve, reject) => {
        let attempts = 0;
        uInterval(() => {
            if (attempts >= maxAttempts) {
                reject(new Error('Not Found'));
                return false;
            }

            const ret = fnWhenSthHasValue(attempts);
            if (ret) {
                resolve(ret);
                return false;
            } else {
                attempts++;
                return true;
            }
        }, delay);
    });

export function uInterval(func, delay, ...args) {
    let timer = null;

    const callback = (...params) => {
        timer = null;

        let ret = null;
        try {
            ret = func(...params);
        } catch (error) {
            // console.error(error);
            return;
        }

        if (ret && hasFunction(ret.then)) {
            ret.then(isContinue => {
                if (isContinue === true) {
                    // 第 N 次延时执行
                    timer = window.setTimeout(callback, delay, ...args);
                }
            });
            return;
        }

        if (ret === true) {
            // 第 N 次延时执行
            timer = window.setTimeout(callback, delay, ...args);
        }
    };

    // 立即执行一次
    callback(...args);

    // 第一次延时执行
    // timer = window.setTimeout(callback, delay, ...args);

    return () => {
        if (timer) {
            window.clearTimeout(timer);
        }

        timer = null;
    };
}
