// import _ from 'lodash';
import BigNumber from 'bignumber.js';

import { hasString, hasNumber } from './helper';

export const CURRENCY_PLACES = 2; // 货币精度
export const DECIMAL_PLACES = 8; // 小数精度
export const DEFAULT_NUMBER = 0; // 默认数字
export const MAX_NUMBER = 100000000;

BigNumber.config({
    // DECIMAL_PLACES,
    ROUNDING_MODE: BigNumber.ROUND_DOWN,
    EXPONENTIAL_AT: [-1e9, 1e9], // [-7, 20],
    // RANGE: [-1e9, 1e9],
    // CRYPTO: false,
    // MODULO_MODE: 1,
    // POW_PRECISION: 0,
    // FORMAT: {
    //   // string to prepend
    //   prefix: '',
    //   // decimal separator
    //   decimalSeparator: '.',
    //   // grouping separator of the integer part
    //   groupSeparator: ',',
    //   // primary grouping size of the integer part
    //   groupSize: 3,
    //   // secondary grouping size of the integer part
    //   secondaryGroupSize: 0,
    //   // grouping separator of the fraction part
    //   fractionGroupSeparator: ' ',
    //   // grouping size of the fraction part
    //   fractionGroupSize: 0,
    //   // string to append
    //   suffix: '',
    // },
    // ALPHABET: '0123456789abcdefghijklmnopqrstuvwxyz',
});

export const BLANK_CHAR = '-';
export const isBlankChar = value => value === BLANK_CHAR;

export const isNumeric = value => /^[-+]?([0-9]+(\.[0-9]*)?|\.[0-9]+)([eE][-+]?[0-9]+)?$/.test(value);
export const isNumericString = value => hasString(value) && isNumeric(value);
export const isNumericValue = value => hasNumber(value) && isNumeric(value);

export const getNumberValue = value =>
    isBlankChar(value) ? BLANK_CHAR : isNumeric(value) ? BigNumber(value).toNumber() : DEFAULT_NUMBER;
export const getNumberString = value =>
    isBlankChar(value) ? BLANK_CHAR : isNumeric(value) ? BigNumber(value).toString() : `${DEFAULT_NUMBER}`;

const generateOperationFn = op => (x, y) =>
    isBlankChar(x) || isBlankChar(y)
        ? BLANK_CHAR
        : isNumeric(x) && isNumeric(y)
        ? BigNumber(x)[op](y).toString()
        : `${DEFAULT_NUMBER}`;

export const bigPlus = generateOperationFn('plus');
export const bigMinus = generateOperationFn('minus');
export const bigMultiply = generateOperationFn('times');
export const bigDivide = generateOperationFn('div');

export const safeOperationFn = op => (x, y) => {
    if (!BigNumber.isBigNumber(x) && !BigNumber.isBigNumber(y) && !isNumeric(x) && !isNumeric(y)) {
        return BLANK_CHAR;
    }
    const X = BigNumber.isBigNumber(x) ? x : BigNumber(isNumeric(x) ? x : 0);
    const Y = BigNumber.isBigNumber(y) || isNumeric(y) ? y : 0;
    return X[op](Y).toString();
};
export const safePlus = safeOperationFn('plus');
export const safeSub = safeOperationFn('minus');
export const safeMul = safeOperationFn('times');
export const safeDiv = safeOperationFn('div');
export const safePow = safeOperationFn('pow');
export const safeUSub = (x, y) => {
    const result = safeSub(x, y);
    return bigGreaterThanOrEqualTo(result, 0) ? result : '0';
};

const generateCompareFn = op => (x, y) => {
    try {
        if (BigNumber.isBigNumber(x)) {
            return x[op](y);
        }

        if (isNumeric(x)) {
            return BigNumber(x)[op](y);
        }
    } catch (err) {
        // console.log(err);
    }

    return false;
};

export const bigGreaterThan = generateCompareFn('gt');
export const bigGreaterThanOrEqualTo = generateCompareFn('gte');
export const bigLessThan = generateCompareFn('lt');
export const bigLessThanOrEqualTo = generateCompareFn('lte');

export const bigToFixed = (value, dp, rm) =>
    isBlankChar(value) ? BLANK_CHAR : isNumeric(value) ? BigNumber(value).toFixed(dp, rm) : `${DEFAULT_NUMBER}`;

export const safeMin = (x, y) => (bigLessThanOrEqualTo(x, y) ? x : y);
export const safeMax = (x, y) => (bigGreaterThanOrEqualTo(x, y) ? x : y);

export const safeAbs = x => (isNumeric(x) ? BigNumber(x).abs() : BLANK_CHAR);

export const safeEq = (x, y) => (isNumeric(x) && isNumeric(y) ? BigNumber(x).eq(BigNumber(y)) : false);

export const SafeBigNumber = value => {
    if (BigNumber.isBigNumber(value)) {
        return BigNumber(value.toString());
    }
    return BigNumber(value);
};

export const operationFn = op => (x, y) => {
    if (!isNumeric(x) || !isNumeric(y)) {
        return BLANK_CHAR;
    }
    const X = BigNumber.isBigNumber(x) ? x : BigNumber(x);
    const Y = BigNumber.isBigNumber(y) ? y : BigNumber(y);
    return X[op](Y).toString();
};
export const plus = operationFn('plus');
export const sub = operationFn('minus');
export const mul = operationFn('times');
export const div = operationFn('div');
export const pow = operationFn('pow');
