import _ from 'lodash';

import { hasString, hasNumber, mergeObject } from './helper';
import { formatNumber4Loans } from './format';
import { isNumeric, bigLessThan, bigGreaterThan, bigToFixed, getNumberValue } from './operation';

// 只显示整数部分
export const formatInteger = (value, options = {}) =>
    formatNumber4Loans(value, mergeObject({}, options, { places: 0 }));
// 无小数位也显示
export const formatFloat = (value, options = {}) =>
    formatNumber4Loans(value, mergeObject({}, options, { placeholder: true }));

// eslint-disable-next-line max-params
export function formatNumberInputValue(value, precision, min, max) {
    if (!hasString(value)) {
        return value;
    }

    // 删除非数字和小数点的字符
    const step01 = _.replace(`${value}`, /[^\d.]/g, '');
    // 过滤多个小数点，只保留第一个小数点
    const step02 = _.split(step01, '.');
    const step03 = step02.length > 2 ? `${step02[0]}.${_.join(_.slice(step02, 1), '')}` : step01;
    // 保留小数精度
    const step04 = _.split(step03, '.');
    const step05 =
        precision > 0 && step04.length > 1 && step04[1].length > precision
            ? `${step04[0]}.${step04[1].substring(0, precision)}`
            : precision === 0
            ? step04[0]
            : step03;
    // 最小值
    const step06 = isNumeric(min) && bigLessThan(step05, min) ? bigToFixed(min) : step05;
    // 最大值
    const step07 = isNumeric(max) && bigGreaterThan(step06, max) ? bigToFixed(max) : step06;
    // console.log(value, step01, step02, step03, step04, step05, step06, step07);
    return `${step07}`;
}

// eslint-disable-next-line max-params
export function ellipsizeText(text, max, mode = 'tail', symbol = '…') {
    if (!hasString(text) || !hasNumber(max) || !hasString(symbol)) {
        return text;
    }

    const maxLength = getNumberValue(max);
    if (text.length <= maxLength || symbol.length >= maxLength) {
        return text;
    }

    const over = maxLength - symbol.length;
    const startAt = getNumberValue(over / 2) + (over % 2);

    let result = text;
    switch (mode) {
        case 'head':
            result = `${symbol}${text.slice(text.length - over)}`;
            break;
        case 'middle':
            result = `${text.slice(0, startAt)}${symbol}${text.slice(startAt === over ? text.length : startAt - over)}`;
            break;
        case 'tail':
        default:
            result = `${text.slice(0, over)}${symbol}`;
            break;
    }
    return result;
}
