import _ from 'lodash';
import React from 'react';
import logo from './logo.png';
// import './App.css';
// import 'bootstrap/dist/css/bootstrap.css';
// import 'bootstrap/less/bootstrap.less';
import './App.less';
import WalletModel from './wallet';
import { ellipsizeText, formatNumberInputValue } from './utils/input';
import { getAddress } from './utils/store';
import { hasString, getValue, hasPlainObject } from './utils/helper';
import { formatCurrency4Loans, formatNumber4Loans, formatPercentages4Loans } from './utils/format';
import { bigGreaterThanOrEqualTo, bigGreaterThan, BLANK_CHAR, isNumeric, safeSub, mul } from './utils/operation';
import { MAX_UINT256, useIntervalRefresh } from './wallet/tools';
import { POOLS } from './wallet/pools';

const TARGET_CHAINID = [42161];

const Header = React.memo(props => {
    const { connect, address, network } = WalletModel.useContext();
    const handleClick = React.useCallback(() => {
        if (!hasString(address)) {
            connect();
        }
    }, [connect, address]);

    let text = 'Connect';
    if (hasString(address)) {
        if (_.includes(TARGET_CHAINID, network?.chainId)) {
            text = ellipsizeText(address, 12, 'middle');
        } else {
            text = 'Wrong Network';
        }
    }

    return (
        <div className="aHeader">
            <img className="logoImg" src={logo} alt="logo" />
            <button type="button" className="btn btn-default" onClick={handleClick}>
                {text}
            </button>
        </div>
    );
});

const Warning = React.memo(props => {
    const { text } = props ?? {};
    return (
        <div className="warningWrap">
            <div className="warning">Warning:</div>
            <div>{text}</div>
        </div>
    );
});

const Link = React.memo(props => {
    const { text, link } = props ?? {};
    return (
        <a className="A-link" href={link} target="_blank" rel="noreferrer">
            {text}
        </a>
    );
});

const Pool = React.memo(props => {
    const { data } = props ?? {};
    const { address, network, approve, stakeTokenInfo, stake, withdraw, earned, claim, totalClaimed, apr } =
        WalletModel.useContext();

    const verify = React.useCallback(() => {
        if (!hasString(address)) {
            alert('connect wallet first');
            return false;
        }
        if (!_.includes(TARGET_CHAINID, network?.chainId)) {
            alert('wrong network');
            return false;
        }
        return true;
    }, [network, address]);

    const [stakeToken, setStakeToken] = React.useState();
    const [earn, setEarn] = React.useState();
    const [aprDay, setAprDay] = React.useState();

    const refresh = React.useCallback(async () => {
        try {
            const _stakeToken = await stakeTokenInfo(data?.contract);
            setStakeToken(_stakeToken);
            const _earn = await earned(data?.contract);
            const _claimed = await totalClaimed(data?.contract);
            setEarn(safeSub(_earn, _claimed));
            const _apr = await apr(data?.contract, data?.name);
            setAprDay(_apr);
        } catch (err) {
            // console.log(err);
        }
    }, [stakeTokenInfo, earned, data, totalClaimed, apr]);

    useIntervalRefresh(refresh);

    const [stakeAmount, setStakeAmount] = React.useState('');
    const handleStakeInput = React.useCallback(
        event => {
            const newValue = _.trim(`${getValue(event, 'target.value', '')}`);
            const value = formatNumberInputValue(newValue, stakeToken?.decimals);
            setStakeAmount(value);
        },
        [stakeToken]
    );

    const handleStakeAll = React.useCallback(() => {
        setStakeAmount(stakeToken?.walletBalance);
    }, [stakeToken]);

    const [withdrawAmount, setWithdrawAmount] = React.useState('');
    const handleWithdrawInput = React.useCallback(
        event => {
            const newValue = _.trim(`${getValue(event, 'target.value', '')}`);
            const value = formatNumberInputValue(newValue, stakeToken?.decimals);
            setWithdrawAmount(value);
        },
        [stakeToken]
    );

    const handleWithdrawAll = React.useCallback(() => {
        setWithdrawAmount(stakeToken?.stakeBalance);
    }, [stakeToken]);

    const handleApprove = React.useCallback(async () => {
        if (!verify()) {
            return;
        }
        try {
            const tx = await approve(stakeToken?.address, data?.contract, MAX_UINT256);
            await tx?.wait();
            const _stakeToken = await stakeTokenInfo(data?.contract);
            setStakeToken(_stakeToken);
        } catch (err) {
            // console.log(err);
            alert(err?.message);
        }
    }, [verify, approve, stakeToken, data, stakeTokenInfo]);

    const handleStake = React.useCallback(async () => {
        if (!verify()) {
            return;
        }
        try {
            if (bigGreaterThan(stakeAmount, stakeToken?.walletBalance)) {
                alert('Balance not enough');
                return;
            }
            const tx = await stake(data?.contract, stakeAmount);
            await tx?.wait();
            setStakeAmount('');
            const _stakeToken = await stakeTokenInfo(data?.contract);
            setStakeToken(_stakeToken);
        } catch (err) {
            // console.log(err);
            alert(err?.message);
        }
    }, [verify, stakeToken, stake, data, stakeAmount, stakeTokenInfo]);

    const handleWithdraw = React.useCallback(async () => {
        if (!verify()) {
            return;
        }
        try {
            if (bigGreaterThan(withdrawAmount, stakeToken?.stakeBalance)) {
                alert('Withdraw exceeding your stake balance');
                return;
            }
            const tx = await withdraw(data?.contract, withdrawAmount);
            await tx?.wait();
            setWithdrawAmount('');
            const _stakeToken = await stakeTokenInfo(data?.contract);
            setStakeToken(_stakeToken);
        } catch (err) {
            // console.log(err);
            alert(err?.message);
        }
    }, [verify, stakeToken, withdraw, data, withdrawAmount, stakeTokenInfo]);

    const handleClaim = React.useCallback(async () => {
        if (!verify()) {
            return;
        }
        try {
            const tx = await claim(data?.contract);
            await tx?.wait();
            const _stakeToken = await stakeTokenInfo(data?.contract);
            setStakeToken(_stakeToken);
        } catch (err) {
            // console.log(err);
            alert(`reward claimed hasn't started yet`);
        }
    }, [verify, claim, data, stakeTokenInfo]);

    const stakeSymbol = hasString(stakeToken?.symbol) ? stakeToken?.symbol : BLANK_CHAR;

    let approveVerify = false;
    if (hasString(stakeAmount)) {
        approveVerify = bigGreaterThanOrEqualTo(stakeToken?.allowance, stakeAmount);
    } else {
        approveVerify = bigGreaterThan(stakeToken?.allowance, 0);
    }
    if (data?.noneedApprove) {
        approveVerify = true;
    }

    let extra;
    if (hasPlainObject(data?.extra?.warning)) {
        extra = <Warning {...data?.extra?.warning} />;
    }
    if (hasPlainObject(data?.extra?.link)) {
        extra = <Link {...data?.extra?.link} />;
    }
    return (
        <div className="panel panel-primary">
            <div className="panel-heading">
                <h3 className="panel-title">
                    {`${data?.name} Pool`}
                    <i className="smalli">FARM</i>
                </h3>
            </div>
            <div className="panel-body">
                <b>APR: </b>
                {/* {formatPercentages4Loans(mul(aprDay?.apr, 365))} */}0
            </div>
            <div className="aRow1">
                {approveVerify ? (
                    <button
                        type="button"
                        className="btn btn-primary"
                        onClick={handleStake}
                        disabled={!isNumeric(stakeAmount) || bigGreaterThanOrEqualTo(0, stakeAmount)}
                    >
                        Stake
                    </button>
                ) : (
                    <button type="button" className="btn btn-primary" onClick={handleApprove}>
                        Approve
                    </button>
                )}
                <div className="A-input-group">
                    <input type="text" className="A-form-control" value={stakeAmount} onChange={handleStakeInput} />
                    <span className="A-input-group-addon" onClick={handleStakeAll}>
                        MAX
                    </span>
                </div>
            </div>
            <div className="aRow1">
                <button
                    type="button"
                    className="btn btn-primary"
                    onClick={handleWithdraw}
                    disabled={!isNumeric(withdrawAmount) || bigGreaterThanOrEqualTo(0, withdrawAmount)}
                >
                    Withdraw
                </button>
                <div className="A-input-group">
                    <input
                        type="text"
                        className="A-form-control"
                        value={withdrawAmount}
                        onChange={handleWithdrawInput}
                    />
                    <span className="A-input-group-addon" onClick={handleWithdrawAll}>
                        MAX
                    </span>
                </div>
            </div>
            <div className="aRow2">
                <div>Your Stake:</div>
                <div>{`${formatNumber4Loans(stakeToken?.stakeBalance)} ${stakeSymbol}`}</div>
            </div>
            <div className="aRow2">
                <div>Your Rewards:</div>
                <div className="aRow4">
                    <div>{`${formatNumber4Loans(earn)} ACHESS`}</div>
                    <button type="button" className="btn btn-default claimBtn" onClick={handleClaim}>
                        Claim
                    </button>
                </div>
            </div>
            <div className="aRow2">
                <div>Daily Rewards:</div>
                <div>{/* {`${formatNumber4Loans(aprDay?.rate)} ACHESS`} */}0</div>
            </div>
            <div className="aRow2">
                <div>{`${data?.name} Balance:`}</div>
                <div>{`${formatNumber4Loans(stakeToken?.walletBalance)} ${stakeSymbol}`}</div>
            </div>
            <div className="aRow2">
                <div>Total Staked:</div>
                <div>{`${formatNumber4Loans(stakeToken?.totalStaked)} ${stakeSymbol}`}</div>
            </div>
            {extra}
        </div>
    );
});
const Status = React.memo(props => {
    const { tvl, chessPrice } = WalletModel.useContext();

    const [tvlData, setTvlData] = React.useState();
    const [price, setPrice] = React.useState();
    const refresh = React.useCallback(async () => {
        const _price = await chessPrice();
        setPrice(_price);
        const _tvl = await tvl();
        setTvlData(_tvl);
    }, [chessPrice, tvl]);
    useIntervalRefresh(refresh);

    const tvlETH = formatNumber4Loans(tvlData?.tvlETH, { places: 2 });
    const tvlUSD = formatCurrency4Loans(tvlData?.tvlUSD, { thousands: true });

    return (
        <div className="panel panel-primary">
            <div className="aRow3">
                <div>TVL: </div>
                <div>{`${tvlETH} ETH = ${tvlUSD}`}</div>
            </div>
            <div className="aRow2">
                <div>ACHESS Price:</div>
                <div>{formatCurrency4Loans(price, { places: 6 })}</div>
            </div>
            {/* <div className="aRow2">
                <div>Circulating Supply:</div>
                <div>2004220 ACHESS</div>
            </div>
            <div className="aRow2">
                <div>Circulating Market Cap:</div>
                <div>$11,892,914.25</div>
            </div> */}
        </div>
    );
});

const Footer = React.memo(props => {
    return (
        <div className="jumbotron text-center" style={{ padding: 0 }}>
            <p>
                <a href="https://bridge.arbitrum.io/" target="_blank" rel="noreferrer">
                    Bridge
                </a>{' '}
                |{' '}
                <a href="https://twitter.com/ArbiChess1" target="_blank" rel="noreferrer">
                    Twitter
                </a>{' '}
                |{' '}
                <a href="https://t.me/arbichess" target="_blank" rel="noreferrer">
                    Telegram
                </a>{' '}
                | <a href="./info.html">Info</a>
            </p>
        </div>
    );
});

const AutoConnect = React.memo(props => {
    const { connect } = WalletModel.useContext();
    if (hasString(getAddress())) {
        connect();
    }
    return null;
});

function App() {
    return (
        <div className="App">
            <AutoConnect />
            <Header />
            <div className="AContent">
                {_.map(POOLS, (pool, index) => (
                    <Pool data={pool} key={index} />
                ))}
                <Status />
                <Footer />
            </div>
        </div>
    );
}

export default App;
